.blog {
  padding: 140px 0 120px;
  background-color: #f0f5ff;

  &__main {
    display: flex;
    justify-content: space-between;
    margin-bottom: 56px;

    label {
      position: relative;
      height: 56px;

      button {
        position: absolute;
        left: 12px;
        top: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        width: 30px;
        height: 30px;
        border: none;
        background-color: transparent;
        transform: translateY(-50%);
      }

      input {
        padding: 16px 16px 16px 50px;
        width: 588px;
        height: 56px;
        outline: none;
        border: 1px solid #bed4ff;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: -0.04em;
        color: #011d47;
        background-color: #fff;

        &::placeholder {
          font-weight: 500;
          font-size: 14px;
          line-height: 24px;
          letter-spacing: -0.04em;
          color: #767e8b;
        }
      }
    }
  }

  &__list {
    display: grid;
    grid-gap: 48px 26px;
    grid-template-columns: 1fr 1fr;
    padding-top: 48px;
    border-top: 1px solid #abb9ca;

    li {
      a {
        width: 100%;
      }

      img {
        width: 100%;
        height: 382px;
        max-height: 382px;
        object-fit: cover;
      }

      &:nth-child(odd) {
        .blog-item__descr {
          p {
            color: #011d47;
          }
        }
      }
    }

    .blog-item__descr {
      margin: 24px 0 16px;

      p {
        + p {
          &::after {
            background-color: #767e8b;
          }
        }
      }
    }
  }

  &__pagination {
    display: flex;
    justify-content: center;
    margin-top: 88px;

    li {
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 48px;
        height: 48px;
        border: 1px solid #abb9ca;
        font-weight: 600;
        font-size: 14px;
        line-height: 24px;
        background-color: transparent;
        transition: all 0.3s;

        svg {
          path {
            transition: all 0.3s;
          }
        }

        &:hover {
          border: 1px solid #011d47;
          color: #fff;
          background-color: #011d47;

          svg {
            path {
              stroke: #fff;
            }
          }
        }
      }

      + li {
        margin-left: 16px;
      }
    }

    .pag-active {
      a {
        border: 1px solid #011d47;
        color: #fff;
        background-color: #011d47;
      }
    }

    .pag-inactive {
      a {
        svg {
          path {
            stroke: #abb9ca;
          }
        }

        &:hover {
          border: 1px solid #abb9ca;
          background-color: transparent;

          svg {
            path {
              stroke: #abb9ca;
            }
          }
        }
      }
    }
  }
}

.blog__list-second {
  grid-gap: 48px 24px;
  grid-template-columns: 1fr 1fr 1fr;

  li {
    &:first-child {
      grid-column: 1 / -1;
    }
  }
}

.blog-contact {
  padding: 120px 0;

  h2 {
    @include rv-line;

    margin: 0 auto 74px;
    max-width: 696px;
    text-align: center;

    &::after {
      left: 50%;
      transform: translateX(-50%);
    }
  }
}