.terms {
  padding: 140px 0 120px;

  h1,
  h2 {
    margin-bottom: 64px;
  }

  h3 {
    margin-bottom: 24px;

    &:not(:first-of-type) {
      margin-top: 56px;
      padding-top: 56px;
      border-top: 1px solid #abb9ca;
    }
  }

  h5 {
    margin: 56px 0 24px;
  }

  &__wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-top: 68px;
    border-top: 1px solid #abb9ca;

    .terms__main {
      width: 100%;
      max-width: 894px;

      img {
        margin-bottom: 56px;
        width: 100%;
        object-fit: cover;
      }

      a {
        margin-bottom: 28px;
        color: #09268d;
      }

      ul,
      ol {
        li {
          &:not(:last-child) {
            margin-bottom: 24px;
          }
        }

        &:not(:last-child) {
          margin-bottom: 56px;
        }
      }

      ol {
        padding-left: 28px;

        li {
          padding-left: 5px;
          color: #394b65;

          &::marker {
            content: counter(list-item) "\a0\a0";
            font-weight: 600;
            font-size: 24px;
            line-height: 32px;
            letter-spacing: -0.04em;
          }
        }
      }

      ul {
        li {
          position: relative;
          padding-left: 28px;

          &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 6px;
            width: 12px;
            height: 12px;
            border-radius: 50%;
            background-color: #09268d;
          }
        }
      }

      p {
        color: #394b65;

        &:not(:last-child) {
          margin-bottom: 28px;
        }
      }

      span {
        background-color: #ff535e;
      }

      small {
        background-color: #09268d;
      }

      span,
      small {
        display: block;
        margin-top: 56px;
        padding: 40px;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0;
        color: #fff;

        &:not(:last-child) {
          margin: 56px 0;
        }
      }

      blockquote {
        margin: 56px 0;
        padding: 40px;
        border-left: none;
        background-color: #f0f5ff;

        img {
          margin: 0;
          max-width: 32px;

          &:last-of-type {
            margin-left: auto;
          }
        }

        p {
          margin: 24px 0;
          font-weight: 600;
          font-size: 24px;
          line-height: 32px;
        }
      }
    }
  }
}